.messageSender {
  display: flex;
  margin-top: 30px;
  flex-direction: column;
  background-color: white;
  border-radius: 15px;
  width: 100%;
  box-shadow: 0 5px 17px -7px rgba(0, 0, 0, 0.75);
}

.messageSender_top {
  display: flex;
  border-bottom: 1px solid #eff2f5;
  padding: 15px;
}

.messageSender_top form {
  flex: 1;
  display: flex;
}

.messageSender_top form input {
  outline-width: 0;
  border: none;
  padding: 5px 20px;
  margin: 0 10px;
  border-radius: 999px;
  background-color: #eff2f5;
}

.messageSender_top form button {
  display: none;
}

.messageSender_input {
  flex: 1;
}

.messageSender_bottom {
  display: flex;
  justify-content: space-evenly;
}

.messageSender_option {
  padding: 20px;
  display: flex;
  align-items: center;
  color: grey;
  margin: 5px;
}

.messageSender_option h3 {
  font-size: medium;
  margin-left: 10px;
  cursor: pointer;
}

.messageSender_option:hover {
  background-color: #eff2f5;
  border-radius: 20px;
}
