.post {
  width: 100%;
  margin-top: 15px;
  border-radius: 15px;
  background-color: white;
  box-shadow: 0 5px 17px -7px rgba(0, 0, 0, 0.75);
  overflow: hidden;
}

.post_top {
  display: flex;
  position: relative;
  align-items: center;
  padding: 15px;
}

.post_top h3 {
  font-size: medium;
}
.post_top p {
  font-size: small;
  color: grey;
}

.post_bottom {
  margin-top: 10px;
  margin-bottom: 10px;
  padding: 15px 25px;
}

.post img {
  width: 100%;
  /* padding: 15px; */
}

.post_options {
  padding-top: 10px;
  border-top: 1px solid lightgray;
  display: flex;
  justify-content: space-evenly;
  font-size: medium;
  color: grey;
  cursor: pointer;
  padding: 15px;
}

.post_option {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 5px;
  flex: 1;
}

.post_option p {
  margin-left: 10px;
}

.post_option:hover {
  background-color: #eff2f5;
  border-radius: 10xp;
}
